import React, { useState, useEffect, useRef } from 'react';
import Canvas from '../components/Canvas/Canvas';
import './Home.css';
import calculateLayoutFromFile from '../services/api';

const Home = () => {
  const [open, setOpen] = useState(false);
  const [uploadStatus, setUploadStatus] = useState('idle');
  const fileInputRef = useRef(null);
  const [graphData, setGraphData] = useState({
    nodes: [],
    edges: []
  });

  const toggleDropdown = () => setOpen(prev => !prev);
  
  useEffect(() => {
    const close = (e) => {
      if (!e.target.closest('.dropdown')) {
        setOpen(false);
      }
    };
    if (open) {
      document.addEventListener('click', close);
    }
    return () => document.removeEventListener('click', close);
  }, [open]);

  const uploadFile = async (file) => {
    if (!file) return;
    
    setUploadStatus('uploading');
    
    try {
      const coordinates = await calculateLayoutFromFile(file);

      const nodes = coordinates.nodes || [];
      const edges = coordinates.edges || [];
      
      const minY = Math.min(...nodes.map(n => n.y));
      const maxY = Math.max(...nodes.map(n => n.y));
      const converted = nodes.map(n => ({
        ...n,
        y: maxY - n.y + minY,
      }));
      
      setGraphData({
        nodes: converted,
        edges: edges
      });
      
      setUploadStatus('success');
      
      setTimeout(() => {
        setUploadStatus('idle');
      }, 2000);
      
      return converted;
    } catch (error) {
      setUploadStatus('error');
      console.error(' :', error);

      setTimeout(() => {
        setUploadStatus('idle');
      }, 2000);
      
      throw error;
    }
  };

  const handleFileSelect = async (event) => {
    const file = event.target.files?.[0];
    if (!file) return;
    
    try {
      await uploadFile(file);
    } catch (error) {
      console.error('Failed to upload file:', error);
    }
  };

  const triggerFileInput = () => {
    setOpen(false); 
    fileInputRef.current?.click();
  };

  const exportGraph = () => {
    alert('');
    setOpen(false);
  };

  const saveGraph = () => {
    alert('');
    setOpen(false);
  };

  const deleteAll = () => {
    setGraphData({ nodes: [], edges: [] });
    setOpen(false);
  };

  return (
    <div className="main-container">
      <div className='control-board'>
        <div className="dropdown">
          <button onClick={toggleDropdown} className="btn">  </button>

          <div className={`dropdown-content ${open ? 'show' : ''}`}>
            <a href="#" onClick={triggerFileInput}> </a>
            <a href="#" onClick={exportGraph}> </a>
            <a href="#" onClick={saveGraph}> </a>
            <a href="#" onClick={deleteAll}> </a>
          </div>
        </div>
        <button className='btn'></button>
        <button className='btn'> </button>
        <button className='btn'> </button>
        <button className='btn'></button>
        <button className='btn'> </button>
        
        <input
          type="file"
          ref={fileInputRef}
          onChange={handleFileSelect}
          accept=".dot"
          style={{ display: 'none' }}
        />
        
        {uploadStatus === 'uploading' && (
          <div className="upload-status uploading">
             ...
          </div>
        )}
        
        {uploadStatus === 'success' && (
          <div className="upload-status success">
              !
          </div>
        )}
        
        {uploadStatus === 'error' && (
          <div className="upload-status error">
              
          </div>
        )}
      </div>
      <Canvas 
        graphData={graphData} 
        onGraphDataChange={setGraphData}
      />
    </div>
  );
};

export default Home;